
.title[data-v-3] {
  font-weight: bold;
  margin-top: 24px;
}
.choice[data-v-3] {
  width: 50%;
  padding: 14px;
  display: inline-block;
  cursor: pointer;
  transform: scale(1);
  transition: transform 100ms ease-in-out;
  box-sizing: border-box;
}
.choice[data-v-3]:hover {
  transform: scale(1.1);
  transition: transform 100ms ease-in-out;
}
.choice img[data-v-3] {
  width: 100%;
}



